import Title from '../Title'
import IconTextCard from '../Cards/IconText'

export default {
  components: {
    'title-main': Title,
    IconTextCard,
    Slick: () => import('vue-slick')
  },
  data() {
    return {
      loaded: false,
      slickOptions: {
        dots: false,
        infinite: false,
        speed: 500,
        autoplay: false,
        slidesToShow: 3,
        arrows: false,
        variableWidth: true,
        pauseOnHover: true,
        responsive: [
          {
            breakpoint: 960,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 1
            }
          },
          {
            breakpoint: 800,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 1
            }
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1
            }
          }
        ]
      },
      services: [
        {
          icon: 'ion-ios-color-wand',
          name: 'Frontend developer',
          desc: 'Using VueJS, React.'
        },
        {
          icon: 'ion-social-dribbble-outline',
          name: 'Backend developer',
          desc: 'Pellentesque ac  vel blandit nulla.'
        },
        {
          icon: 'ion-ios-world-outline',
          name: 'Databases',
          desc: 'Wide experience'
        }
        // {
        //   icon: 'ion-ios-camera-outline',
        //   name: 'Photo Editing',
        //   desc: 'Pellentesque ac  vel blandit nulla.'
        // },
        // {
