import Hidden from '../Hidden'
import AvatarBuble from './AvatarBuble'
import testiData from './testimonialsData'
import Title from '../Title'

export default {
  components: {
    'title-main': Title,
    AvatarBuble,
    Hidden,
    Slick: () => import('vue-slick')
  },
  data() {
    return {
      loaded: false,
      testiData: testiData,
      popupData: {},
      slickOptions: {
        dots: false,
        infinite: false,
        speed: 500,
        autoplay: false,
        slidesToShow: 1,
        arrows: false
      }
    }
  },
  mounted() {
    this.loaded = true
  },
  computed: {
    firsthChar() {
      return txt => txt.charAt(0)
    }
  }
}
