export default {
  components: {
    ImageThumbCard,
    'title-main': Title,
    Hidden,
    Slick: () => import('vue-slick')
  },
  data() {
    return {
      data: [],
      isLoaded: true,
      loaded: false,
      filter: 'all',
      slickOptions: {
        dots: false,
        infinite: false,
        speed: 500,
        autoplay: false,
        slidesToShow: 1,
        arrows: false
      }
    }
  },
  mounted() {
    this.data = portfolio.sort(() => Math.random() - 0.5)
    this.loaded = true
  },
  methods: {
    filterChildren(name) {
      if (name !== 'all') {
        const filteredData = portfolio.filter(item => item.category === name)
        this.data = filteredData
        this.filter = name
      } else {
        this.data = portfolio
        this.filter = 'all'
      }

      this.isLoaded = false
      setTimeout(() => {
        this.isLoaded = true
      }, 700)

      // re-init slick carousel for mobile
      if (this.isMobile) {
        this.$nextTick(() => {
          this.$refs.slick.reSlick()
        })
      }
    }
  },
  computed: {
    isMobile() {
      const xsDown = this.$store.state.breakpoints.xsDown
      return xsDown.indexOf(this.$mq) > -1
    }
  }
}
